/* ==========================================================================
   M5 NORMALIZE
   ========================================================================== */

*,
*::after,
*::before {
  box-sizing: border-box;
  -webkit-text-stroke-width: 0;
  -webkit-text-stroke-color: transparent;
}

input::-ms-clear,
input::-ms-reveal,
::-ms-reveal {
  display: none;
}

header,
section,
footer,
aside,
nav,
main,
article,
figure {
  display: block;
}

label,
button {
  -webkit-tap-highlight-color: rgba(#000, 0);
}

button,
input,
textarea {
  -webkit-appearance: none;
  font-size: 100%;
  border: 0;
  outline: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

html {
  min-height: 100%;
  font-size: 62.5%;
  position: relative;
}

body {
  width: 100%;
  &.no-transition {
    overflow: hidden;
    * {
      transition: none !important;
    }
  }
  &.remove-scroll {
    overflow: hidden;
  }
  &.remove-scroll-ios {
    overflow: hidden;
    @media only screen and (max-width: 767px) {
      position: fixed;
    }
  }
}

a {
  color: blue;
  cursor: pointer;
  outline: 0;
  &:hover {
    color: darken(blue, 25%);
  }
}

a img {
  border: none;
}

input,
textarea {
  outline: 0;
}

textarea {
  resize: none;
  overflow: auto;
}

strong,
b {
  font-weight: bold;
}

em,
i {
  font-style: italic;
}

u {
  text-decoration: underline;
}

img {
  max-width: 100%;
  font-size: 1.2rem;
}

p,
ul,
ol,
table,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.3;
}

ul,
ol {
  padding: 0;
  margin: 0;
  li {
    margin: 0;
    padding: 0 0 0 12px;
  }
}

ol {
  margin-left: 22px;
  list-style-type: decimal;
  ol {
    list-style-type: lower-alpha;
    ol {
      list-style-type: decimal;
      ol {
        list-style-type: lower-alpha;
        ol {
          list-style-type: decimal;
        }
      }
    }
  }
  table {
    margin: 16px 0;
  }
}

ul {
  margin-left: 32px;
  list-style-type: square;
  li {
    padding-left: 8px;
  }
  ul {
    list-style-type: disc;
    ul {
      list-style-type: square;
      ul {
        list-style-type: disc;
        ul {
          list-style-type: square;
        }
      }
    }
  }
  table {
    margin: 16px 0;
  }
}
